.calendar {
  margin-top: 100px;

  &__field {
    display: flex;
    flex-wrap: wrap;
    width: $max-width;
    padding: $cell-padding;
    gap: $cell-gap;
  }

  &__day {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 30px;
    width: $cell-size;
    height: $cell-size;
    background-color: #eee;
    border: 1px solid black;
    transition-duration: 0.5s;

    &:hover {
      cursor: pointer;
      background-color: pink;
      transform: translateY(-20px);
    }

    @for $index from 1 through 31 {
      &:nth-child(#{$index})::before {
        content: "#{$index}";
      }
    }
  }
}

@for $index from 28 through 31 {
  .calendar__field--month-length--#{$index} :nth-child(n + #{$index + 1}) {
    display: none;
  }
}

@each $day, $number in $days {
  .calendar__field--start-day--#{$day} {
    .calendar__day:first-child {
      margin-left: ($cell-size + $cell-gap) * $number;
    }
  }
}
